/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { Component } from 'react';
export default class Header extends Component {
  render() {
    return (
      <React.Fragment>
{/* Resume Section
   ================================================== */}
<section id="resume">
  {/* Education
----------------------------------------------- */}
  <div className="row education">
    <div className="three columns header-col">
      <h1><span>Education</span></h1>
    </div>
    <div className="nine columns main-col">
      <div className="row item">
        <div className="twelve columns">
          <h3>Instituto Politécnico de Castelo Branco</h3>
          <p className="info">Computer Engineering<span>•</span> <em className="date">2014-2015</em></p>
          <p>
            Erasmus student exchange program.
          </p>
        </div>
      </div> {/* item end */}
      <div className="row item">
        <div className="twelve columns">
          <h3>Ondokuz Mayıs University</h3>
          <p className="info">Computer Engineering <span>•</span> <em className="date">2011-2015</em></p>
          <p>
            Technical CE bachelor degree.
          </p>
        </div>
      </div> {/* item end */}
    </div> {/* main-col end */}
  </div> {/* End Education */}
  {/* Work
----------------------------------------------- */}
  <div className="row work">
    <div className="three columns header-col">
      <h1><span>Work</span></h1>
    </div>
    <div className="nine columns main-col">
      <div className="row item">
        <div className="twelve columns">
          <h4>Intertech</h4>
          <p className="info">Software Engineer<span>•</span> <em className="date">2018 - Present</em></p>
        </div>
      </div> {/* item end */}
      <div className="row item">
        <div className="twelve columns">
          <h4>Ekol Logistics</h4>
          <p className="info">Software Engineer<span>•</span> <em className="date">2017 - 2018</em></p>
        </div>
      </div> {/* item end */}
      <div className="row item">
        <div className="twelve columns">
          <h4>Strategia IT</h4>
          <p className="info">Software Engineer<span>•</span> <em className="date">2015 - 2017</em></p>
        </div>
      </div> {/* item end */}
    </div> {/* main-col end */}
  </div> {/* End Work */}
  {/* Skills
----------------------------------------------- */}
  <div className="row skill">
    <div className="three columns header-col">
      <h1><span>Skills</span></h1>
    </div>
    <div className="nine columns main-col">
      <div className="bars">
        <ul className="skills">
          <li><span className="bar-expand html5" /><em>HTML5</em></li>
          <li><span className="bar-expand photoshop" /><em>jQuery</em></li>
          <li><span className="bar-expand css" /><em>C#</em></li>
          <li><span className="bar-expand photoshop" /><em>CSS</em></li>
          <li><span className="bar-expand jquery" /><em>React</em></li>
        </ul>
      </div>{/* end skill-bars */}
    </div> {/* main-col end */}
  </div> {/* End skills */}
</section> {/* Resume Section End*/}
     </React.Fragment>
     );
   }
 }
